.dialog-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
}

.dialog {
  z-index: 1;
  background-color: white;
  box-shadow: 0px 11px 15px -7px rgb(0 0 0 / 20%), 0px 24px 38px 3px rgb(0 0 0 / 14%), 0px 9px 46px 8px rgb(0 0 0 / 12%);
  padding: 70px 100px;
}

.dialog__header {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  color: var(--color-black);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.dialog__title {
  margin: 0;
  font-size: 30px;
}

.dialog__close {
  background-color: transparent;
  font-size: 2rem;
  padding: 8px;
  border: none;
  cursor: pointer;
  height: 48px;
  width: 48px;
  border-radius: 50%;
}

.dialog__close:hover,
.dialog__close:focus-visible {
  background-color: var(--color-gray);
}

.dialog__close svg {
  width: 1em;
  height: 1em;
}

.dialog__close path {
  fill: currentColor;
}

